body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    padding: 6em 0;
}


.links {
    margin-left: 2em;
}

.yearLink {
    color: lightgray;
    text-decoration: none;
    font-size: 2em;
}

@media screen and (min-width: 768px) {
    .App {
        padding: 10em 0;
    }
}
.NewMemberForm {
    padding: 1em;
    overflow: wrap;
}

form {
    margin-top: 1em;
}

p {
    margin-bottom: 1em;
}
.Member {
    text-align: center;
    margin-bottom: 3em;
}

.deletedMember {
    height: 100%;
    min-height: 5em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.photoMembre {
    width: 70%;
}

@media screen and (min-width: 768px) {
    .Member {
        margin-bottom: 0;
    }

    .photoMembre {
        width: 100%;
        max-width: 250px;
    }
}
@media screen and (min-width: 1280px) {
    .photoMembre {
        width: 80%;
    }
}
.Section {
    margin-bottom: 5em;
    background-color: rgba(0, 0, 0, 0.5);
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.7);
    text-align: center;
    padding: 2em 0;
}

.members {
    margin: 3em 0;
}

@media screen and (min-width: 768px) {
    .Section {
        padding: 2em;
    }

    .members {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-gap: 2em;
    }

    .bureau .members, .musique_et_chant .members {
        grid-template-columns: 1fr 1fr 1fr;
        padding: 0 2em;
    }

    .mise_en_scène .members, .chorégraphie .members {
        grid-template-columns: 1fr 1fr;
        padding: 0 4em;
    }
}
@media screen and (min-width: 1024px) {
    .members {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    }

    .bureau .members {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}
.sections {
    padding: 0 2em;
}

.intro {
    padding: 2em 2em;
    margin: 2em 0;
    margin-bottom: 3em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}
.intro .introText {
    padding: 2em;
    background: rgba(0, 0, 0, 0.7);
    height: 100%;
}
.intro .introText h1 {
    font-size: 3em;
    font-family: Duck in Shipah;
    text-align: center;
    margin-bottom: 0.5em;
}

@media screen and (min-width: 768px) {
    .intro {
        padding: 2em 10%;
    }

    .sections {
        padding: 2em 7em;
    }
}   
@media screen and (min-width: 1024px) {
    .intro {
        padding: 2em 20%;
    }
}
@media screen and (min-width: 1600px) {
    .intro {
        padding: 4em 30%;
    }

    .sections {
        padding: 2em 10em;
    }
}
@media screen and (min-width: 1920px) {
    .intro {
        padding: 6em 30%;
    }
}
